import { useEffect, useState } from "react";

import Helmet from "react-helmet";
import Loader from "react-loader-spinner";

import SearchSuggestions from "./SearchSuggestions";
import { getSuggestionsList } from "../services/searchServices";
import { withRouter } from "react-router-dom";
import { toast } from "react-toastify";
import SearchHistoryMain from "./SearchHistoryMain";
import searchiconImage from "./img/search-icon.png";

const AutoComplete = ({ history }) => {
  //States Comment:------------------------------------------------------------------------------------------
  //activeSuggestion: The item on suggestion box which is selected.
  //---------------
  //filteredSuggestions: A list of search suggestion which is returned by search API.
  //---------------
  //showSuggestions: Allow show Suggestions box.
  //---------------
  //userInput: The autocomplete input value.
  //---------------
  //isLoaded: Use for loading in autocomplete textbox.
  //---------------
  //searchHistoryList: Return a list of user search history
  //---------------
  //suggestionsItemsCount: Return count for show suggestions Itemsfrom user setting.
  //---------------
  //showSearchedHistory: Allow show last top 10 searches history box.
  //---------------
  //topLastSearchedHistoryList: A list of search last top 10 searches history.
  //---------------------------------------------------------------------------------------------------------
  const [activeSuggestion, setSelectedSuggestion] = useState(-1);
  const [filteredSuggestions, setFilteredSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [userInput, setUserInput] = useState("");
  const [isLoaded, setIsLoaded] = useState(true);
  const [searchHistoryList, setSearchHistoryList] = useState([]);
  const [suggestionsItemsCount, setSuggestionsItemsCount] = useState(0);
  const [showSearchedHistory, setShowSearchedHistory] = useState(false);
  const [topLastSearchedHistoryList, setTopLastSearchedHistoryList] = useState(
    []
  );

  //* Show a history of searches by localStorage
  const getSearchesHistory = () => {
    try {
      if (localStorage.getItem("searchHistory")) {
        const searchHistory = JSON.parse(localStorage.getItem("searchHistory"));
        setSearchHistoryList(searchHistory);
        setTopLastSearchedHistoryList(searchHistory.slice(0, 10));
      }
    } catch (error) {
      toast.error("There is a problem on getting search history.");
    }
  };

  //* call search history and get user setting methods in loading
  useEffect(() => {
    //Get user setting SuggestionsItemsCount for show count of data in suggestion.
    const count = localStorage.getItem("suggestionsCount")
      ? localStorage.getItem("suggestionsCount")
      : 10;
    setSuggestionsItemsCount(count);

    getSearchesHistory();
  }, []);

  //---------------------------------------------
  //For search and suggestions in Auto-Complete component used datamuse’s API.
  //Get a suggestion list by API
  //Using datamuse’s API
  //Input parameter: userInput: the search item / suggestionsItemsCount: Count of items for result
  //Output parameter: data
  //---------------------------------------------
  const searchAutoComplete = async (e) => {
    const userInput = e.currentTarget.value;

    try {
      const { data } = await getSuggestionsList(
        userInput,
        suggestionsItemsCount
      );

      setSelectedSuggestion(0);
      setFilteredSuggestions(data);
      setShowSuggestions(true);
      setUserInput(userInput);
    } catch (error) {
      toast.error("There is a problem on getting data.", {
        onOpen: () => toast.dismiss(),
      });
    } finally {
      setIsLoaded(true);
    }
  };

  //* Add search item to user search history.
  const addSearchHistory = (word) => {
    try {
      let suggestion = {
        searchedPhrase: word,
      };

      //add new record to searchHistoryList state
      const tempFilteredSuggestions = [...searchHistoryList];
      suggestion._id = searchHistoryList.length + 1;
      suggestion.searchedAt = new Date();
      tempFilteredSuggestions.push(suggestion);

      setSearchHistoryList(tempFilteredSuggestions);
      setTopLastSearchedHistoryList(tempFilteredSuggestions.slice(0, 10));
      setUserInput("");
      setShowSearchedHistory(false);

      localStorage.setItem(
        "searchHistory",
        JSON.stringify(tempFilteredSuggestions)
      );
    } catch (error) {
      toast.error("There is a problem on adding a search item.");
    }
  };

  //* Delete an item of user search history by itemID
  const deleteHistoryItem = (historyID) => {
    try {
      const tempSearchHistoryList = [...searchHistoryList];
      const filteredSearchHistoryList = tempSearchHistoryList.filter(
        (t) => t._id !== historyID
      );

      localStorage.setItem(
        "searchHistory",
        JSON.stringify(filteredSearchHistoryList)
      );

      setSearchHistoryList(filteredSearchHistoryList);
    } catch (error) {
      toast.error("There is a problem on deleting item");
    }
  };

  //* Delete all of user search history by userID
  const deleteAllHistory = () => {
    try {
      if (searchHistoryList.length === 0) return;

      //clear searchHistoryList state
      setSearchHistoryList([]);
      setTopLastSearchedHistoryList([]);
      localStorage.setItem("searchHistory", []);
    } catch (error) {
      toast.error("There is a problem on deleting history.");
    }
  };

  //* Save user setting in database
  const saveUserSetting = (e) => {
    e.preventDefault();

    try {
      if (suggestionsItemsCount < 1 || suggestionsItemsCount > 40) {
        toast.error("required | min:1 | max:40.");
        return;
      }

      if (suggestionsItemsCount !== localStorage.getItem("suggestionsCount")) {
        localStorage.setItem("suggestionsCount", suggestionsItemsCount);
        toast.success("Setting has been updated successfully.");
      }
    } catch (error) {
      toast.error("Setting has not been updated.");
    }
  };

  //* Call searchAutoComplete method by change autocomplete textbox value
  const handleAutoCompleteonOnChange = (e) => {
    searchAutoComplete(e);
    setIsLoaded(false);
  };

  //* Add searched item on suggestion list by click on suggestions list
  const handleSearchSuggestionOnClick = (e) => {
    setSelectedSuggestion(0);
    setFilteredSuggestions([]);
    setShowSuggestions(false);
    addSearchHistory(e);
  };

  //* handle states in keydown for autocomplete textbox for Enter, Up and down arrow keys, and escape.
  const handleAutoCompleteKeyDown = (e) => {
    if (e.currentTarget.value === "") {
      setSelectedSuggestion(0);
      setFilteredSuggestions([]);
      setShowSuggestions(false);
      setShowSearchedHistory(false);
    }

    //When the enter key is pressed
    if (e.keyCode === 13) {
      if (showSuggestions) {
        setSelectedSuggestion(0);
        setShowSuggestions(false);

        const searchExperssion =
          filteredSuggestions.length > 0
            ? filteredSuggestions[activeSuggestion].word
            : e.currentTarget.value;
        setUserInput(searchExperssion);
        addSearchHistory(searchExperssion);
      } else {
      }
    }

    //When the escape key is pressed
    else if (e.keyCode === 27) {
      setSelectedSuggestion(0);
      setFilteredSuggestions([]);
      setShowSuggestions(false);
      setShowSearchedHistory(false);
    }

    //When the up arrow key is pressed
    else if (e.keyCode === 38) {
      if (activeSuggestion === 0) {
        return;
      }

      if (e.currentTarget.value !== "") {
        setSelectedSuggestion(activeSuggestion - 1);
        setUserInput(filteredSuggestions[activeSuggestion - 1].word);
      }
    }
    //When the down arrow key is pressed
    else if (e.keyCode === 40) {
      if (activeSuggestion - 1 === filteredSuggestions.length) {
        return;
      }

      if (e.currentTarget.value !== "") {
        if (activeSuggestion < filteredSuggestions.length - 1) {
          setSelectedSuggestion(activeSuggestion + 1);
          setUserInput(filteredSuggestions[activeSuggestion + 1].word);
        }
      } else setShowSearchedHistory(true);
    }
  };

  //* Number control and set suggestionsItemsCount state
  const suggestionsItemsCountOnChange = (e) => {
    if (Number(e)) setSuggestionsItemsCount(e);
  };

  //* Hidden suggestion and top 10 history boxes by click any place
  const hiddenSuggestionBox = (e) => {
    setShowSuggestions(false);
    let element = e.target;
    if (element.getAttribute("autocompleteinput") !== "true")
      setShowSearchedHistory(false);
  };

  return (
    <div className="search-container" onClick={(e) => hiddenSuggestionBox(e)}>
      <Helmet>
        <title>Simple Search | AutoComplete</title>
      </Helmet>

      {!suggestionsItemsCount ? (
        <div className="loader">
          <Loader type="Bars" color="#9933cc" height={80} width={80} />
          <h5>Please wait ...</h5>
        </div>
      ) : null}

      <div className="topbar">
        <form className="form-inline" onSubmit={saveUserSetting}>
          <label>Suggestion count </label>
          <input
            type="text"
            id="inputSuggestionCount"
            placeholder="Suggestion count"
            name="suggestionCount"
            value={suggestionsItemsCount}
            onChange={(e) => suggestionsItemsCountOnChange(e.target.value)}
          />
          <button type="submit">Save</button>
        </form>
      </div>

      <div className="search-panel">
        <div
          className={
            showSuggestions && userInput
              ? "search-wrapper search-wrapper-active"
              : showSearchedHistory
              ? "search-wrapper search-wrapper-active"
              : "search-wrapper"
          }
        >
          <div className="search-input">
            <img
              src={searchiconImage}
              alt="search icon"
              className="search-img"
            />
            <input
              type="text"
              autocompleteinput="true"
              placeholder="Search..."
              onChange={handleAutoCompleteonOnChange}
              onKeyDown={handleAutoCompleteKeyDown}
              value={userInput}
              className={
                isLoaded
                  ? showSuggestions
                    ? "input-search input-clicked"
                    : "input-search"
                  : showSuggestions
                  ? "input-search input-loading input-clicked"
                  : "input-search input-loading"
              }
              onClick={(e) => setShowSearchedHistory(true)}
            />
          </div>

          {showSuggestions && userInput ? (
            <SearchSuggestions
              filteredSuggestions={filteredSuggestions}
              activeSuggestion={activeSuggestion}
              handleClick={(e) => handleSearchSuggestionOnClick(e)}
              input={userInput}
              history={false}
            />
          ) : showSearchedHistory ? (
            <SearchSuggestions
              filteredSuggestions={topLastSearchedHistoryList}
              activeSuggestion={activeSuggestion}
              handleClick={(e) => handleSearchSuggestionOnClick(e)}
              input={userInput}
              history={true}
            />
          ) : null}
        </div>

        {searchHistoryList ? (
          <SearchHistoryMain
            searchHistoryList={searchHistoryList}
            handleRemoveHistoryItem={deleteHistoryItem}
            handleDeleteHistory={deleteAllHistory}
          />
        ) : null}
      </div>
    </div>
  );
};

export default withRouter(AutoComplete);
